

.card {
  background: #fff;
  border-radius: 2px;
  height: 260px;
  margin: 1rem;
  position: relative;
  width: 260px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: rgb(167, 167, 167);
}
.card > .img-container > img {
  padding: 10%;
  height: 80%;
}

.card:hover{
  -webkit-box-shadow: 0 12px 12px rgba(23, 95, 143, 0.945), 0 12px 12px rgba(23, 95, 143, 0.945);
          box-shadow: 0 12px 12px rgba(23, 95, 143, 0.945), 0 12px 12px rgba(23, 95, 143, 0.945);
}




